.fixed-action-btn {
	position: fixed;
	z-index: 998;
	right: 35px;
	bottom: 35px;
	margin-bottom: 0;
	padding-top: 15px;
}

.fixed-action-btn ul {
	display: flex;
	flex-direction: column;
	transition: 400ms height;
	position: absolute;
	bottom: 64px;
	height: 64px;
	left: 0;
	right: 0;
	margin: 0 0 15px;
	padding: 0;
	text-align: center;
}

.fixed-action-btn ul li {
	display: flex;
	margin-bottom: 15px;
	margin-left: auto;
	margin-right: auto;
	z-index: 0;
}

.fixed-action-btn ul a.btn-floating {
	opacity: 0;
	transition-duration: 0.4s;
	transform: scale(0.4) translate(0);
}

.fixed-action-btn ul a.btn-floating.shown {
	transform: scale(1) translate(0);
	opacity: 1;
}

.fixed-action-btn.active ul {
	height: 300px;
}

